import pickmeup from './../../pickmeup/pickmeup.js';
addEventListener('DOMContentLoaded', function () {
    pickmeup.defaults.locales['ru'] = {
        days: ['', '', '', '', '', '', ''],
        daysShort: ['', '', '', '', '', '', ''],
        daysMin: ['', '', '', '', '', '', ''],
        months: ['', '', '', '', '', '', '', '', '', '', '', ''],
        monthsShort: ['', '', '', '', '', '', '', '', '', '', '', '']
    };
	pickmeup('.datepicker', {
        flat : true,
        locale: "ru",
        mode: 'range',
        prev: 'arrow_back',
        next: 'arrow_forward'
    });
})